.id-img {
  width: 500px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 5px solid black;
  border-radius: 4px;
}

.set-date {
  display: block;
  /* font-size: 1.6em; */
  font-size: 1.0em;
  font-weight: 700;
	margin: 0 0 .5em;
	color: #333;
}

.btn-primary {
  background-color: rgb(0, 0, 75);
  color: white;
  padding: 10px 15px;
  font-weight: bold;
  font-size: 1em;  
  font-family: source sans pro;
  border: 3px solid black;
  border-radius: 4px;  
}

.btn-danger {
  background-color: rgb(0, 0, 75);
  color: white;
  padding: 10px;
  font-weight: bold;
  font-size: 1em;
  border: 3px solid black;
  border-radius: 4px;
  margin-left: 3px;
}

.save-form h3 {
  margin-top: -10px;
  text-align: center;
}

.save-form {
	background: #bdc3c7;
	padding: 2em 2em 1em;
	max-width: 400px;
	margin: 20px auto 0;
	-webkit-box-shadow: 0 0 1em #222;
	        box-shadow: 0 0 1em #222;
	border-radius: 5px;
}

.form-control {
	margin: 0 0 1em 0;
	position: relative;
}

.form-main label {
	display: block;
  /* font-size: 1.6em; */
  font-size: 1.0em;
  font-weight: 700;
	margin: 0 0 .5em;
	color: #333;
}

.form-main input {
	display: block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
  outline: none;

  background: #f5f5f5;
	border: 1px solid #e5e5e5;
	font-size: 1em;
	padding: .8em .5em;
  border-radius: 5px;
  margin-bottom: 15px;
}


