@import url(https://fonts.googleapis.com/css?family=Open+Sans);

body{
  background: #f2f2f2;
  font-family: 'Open Sans', sans-serif;
}

.trail-instruction {
  margin: 20px 100px;
  padding: 5px 0;
  background-color: lightgrey;
  background: -webkit-gradient(linear, left top, right bottom, from(rgba(170, 170, 170, 0.8)), to(rgba(170, 170, 170, 0.8)) );
  background: -webkit-linear-gradient(left top, rgba(170, 170, 170, 0.8), rgba(170, 170, 170, 0.8) );
  background: -o-linear-gradient(left top, rgba(170, 170, 170, 0.8), rgba(170, 170, 170, 0.8) );
  background: linear-gradient(to right bottom, rgba(170, 170, 170, 0.8), rgba(170, 170, 170, 0.8) );
}


